.form_box {
    display: inline-block;
    width: 300px;
    padding: 10px;
    border: 1px solid;
}

.form_label {
    display: block;
    font-weight: 500;
    font-size: 18px;
    margin: 0 0 5px 0;
}

.form_input {
    display: block;
    width: 180px;
    margin-bottom: 20px;
    font-size: 18px;
    border: none;
    outline: 1px solid rgb(146, 142, 142);
    border-radius: 2px;
}

.form_input:hover,
.form_input:focus {
    outline: 2px solid rgb(135, 168, 235);
}

.form_button {
    display: block;
    width: 100px;
    border: none;
    outline: 1px solid rgb(146, 142, 142);
    background-color: rgb(248, 248, 250);
}

.form_button:hover,
.form_button:focus {
    background-color: rgb(135, 168, 235);
}